import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-proftitle',
  templateUrl: './add-proftitle.component.html',
  styleUrls: ['./add-proftitle.component.css']
})
export class AddProftitleComponent implements OnInit {
  @Input() profTitle: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private schoolService: UniversityService,
    private location: Location
  ) { }


  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void { //多个文本框数据
    this.profTitle = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.schoolService.addProfTitle(this.profTitle)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
